import React from 'react'
import { graphql } from 'gatsby'
import PropTypes from 'prop-types'
import MetaData from '../components/meta/MetaData'

import Layout from '../components/layout'
import LinkButton from '../components/LinkButton'
import Featured from '../components/featured'

const IndexPage = ({ data, location }) => {
  //const siteTitle = data.site.siteMetadata.title
  const posts = data.allMdx.edges

  return (
    <>
      <MetaData
        location={location}
        type="website"
        title={`Digital Fabric attemps the impossiple to improve their craft`}
        keywords={[`David Wawryko`, `Montreal Web Developer`, `Node.js Developer`, `Audio Engineer`, `Music Producer`]}
        description={`David Wawryko is an engineer living in Montreal.`}
        isHome={true}
      />
      <Layout>
        <section id="index" className={`site-section hidden`}>
          <h1 className={`sr-only`}>Digital Fabric</h1>

          <Featured />

          <nav className={`site-nav`}>
            <ul className={`list-nav list-unstyled mb-0`}>
              {posts.map(({ node }) => {
                const title = node.frontmatter.title || node.fields.slug
                const slug = node.fields.slug
                const website = node.frontmatter.website
                const alt = node.frontmatter.alt
                const layout = node.frontmatter.layout
                return <LinkButton key={node.fields.slug} slug={slug} layout={layout} title={title} website={website} alt={alt} />
              })}
            </ul>
          </nav>
        </section>
      </Layout>
    </>
  )
}

export default IndexPage

IndexPage.propTypes = {
  data: PropTypes.node.isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string.isRequired,
  }).isRequired,
}

export const pageQuery = graphql`
  query {
    allMdx(sort: { fields: [frontmatter___weight], order: DESC }, filter: { fields: { sourceInstanceName: {} } }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            layout
            title
            alt
            website
            icon
            weight
          }
        }
      }
    }
  }
`
